import React, { useRef, useEffect } from 'react';
import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';

import Title from '../title/Title';
import Banner from '../banner/Banner';
import Product from '../product/Product';
import Banner2 from '../banner2/Banner2';
import ShopPage1 from '../shopPage1/ShopePage1';
import ShopPage2 from '../shopPage2/ShopPage2';
import './Home.css';

import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'


function App() {
//   --------------------------------------------------------
//   -------------------fadein Effect------------------------
//   --------------------------------------------------------
    gsap.registerPlugin(ScrollTrigger);

    // const titleRef = useRef(null);
    // const bannerRef = useRef(null);
    // const shopRef = useRef(null);

    useEffect(() => {
        gsap.from('.titleComponent',{
            autoAlpha: 0,
            duration: 1,
            y: 100,
        })
        gsap.from('.bannerComponent',{
            autoAlpha: 0,
            duration: 1,
            y: 100,
            delay: 0.5,
        })
        gsap.from('.shopComponent',{
            autoAlpha: 0,
            duration: 1,
            y: 100,
            scrollTrigger: '.shopComponent'
        })
        gsap.from('.Banner2Component',{
            autoAlpha: 0,
            duration: 1,
            y: 100,
            scrollTrigger: '.Banner2Component'
        })
    
    })



//   --------------------------------------------------------
//   -------------------fadein Effect------------------------
//   --------------------------------------------------------

    

  return (
      <div className="App">
        <Router>
            <div className='titleComponent' >
                <Title />
            </div>
            <div className='bannerComponent'>
                <Banner />
            </div>
            <div className='shopComponent'>
                <Switch>
                    <Route path='/Halloween-website' exact component={ShopPage1} />
                    <Route path='/page2' component={ShopPage2} />
                </Switch>
                <Product />
            </div>
            <div className='Banner2Component'>
                <Banner2 />
            </div>
        </Router>
      </div>

  );
}

export default App;
